.sidebar {
    padding: 30px 15px;
}

.recent-title {
    margin-bottom: 20px;
}

.btn-gray {
    background-color: #e6eaf1;
}

.button {
    padding: 10px;
    border-radius: 50px;
    cursor: pointer;
}

.plus {
    font-size: 20px;
}

.new-activity {
    color: gray;
}

.activity-items {
    padding: 10px 15px;
    margin-bottom: 10px;
    animation: fadeIn 2s;
}

.activity-items:hover, .bottom-button:hover {
    background-color: #e6eaf1;
    border-radius: 50px;
}

.bottom-button {
    padding: 10px 12px;
}

.hamburger {
    margin-left: 10px;
}

@media screen and (max-width: 450px) {
    .sidebar{display: none}
}