@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Outfit", sans-serif;
  font-size: 1em;
}

.make-flex {
  display: flex;
}

.space-between {
  justify-content: space-between;
}

.item-center {
  align-items: center;
}

.flex-column {
  flex-direction: column;
}

.self-start {
  align-self: flex-start;
}

.gap-5 {
  gap: 5px;
}
.gap-8 {
  gap: 8px;
}
.gap-10 {
  gap: 10px;
}
.gap-13 {
  gap: 13px;
}
.gap-15 {
  gap: 15px;
}
.gap-18 {
  gap: 18px;
}
.gap-20 {
  gap: 20px;
}

.h-100vh {
  height: 100vh;
}

.light-gray {
  background-color: #f0f4f9;
}

.pointer {cursor: pointer;}

#root {
  animation: fadeIn 1.5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  } 100% {
    opacity: 1;
  }
  
}