.main {
  flex: 1;
}

.top-content {
  padding: 20px 20px;
}

.toptitle {
  font-size: 24px;
  color: gray;
}

.mid-content {
  max-width: 800px;
  margin: auto;
}

.bottom-content {
  width: 790px;
  align-self: center;
}

.footer {
  margin: 15px 0;
  text-align: center;
}

.footer p {
  font-size: 12px;
}

.welcome-title {
  text-align: center;
}

.welcome-title h1,
.welcome-title h2 {
  font-size: 44px;
}

.welcome-title h1 {
  background: -webkit-linear-gradient(16deg, #4b90ff, #ff5546);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline;
}

.cards {
  margin-top: 20px;
}

.card {
  padding: 20px;
  background: aliceblue;
  max-width: 180px;
  border-radius: 10px;
  position: relative;
  min-height: 160px;
}

.card:hover {
  background-color: #e6eaf1;
}

.card-icon {
  position: absolute;
  bottom: 20px;
  right: 20px;
  background: white;
  padding: 10px;
  border-radius: 50%;
}

.card p {
  font-size: 15px;
  line-height: 20px;
  color: #323232;
}

.input-box {
  background-color: #f0f4f9;
  padding: 10px 20px;
  border-radius: 30px;
}

.input-box input {
  flex: 1;
  border: 0;
  outline: 0;
  padding: 8px 10px;
  background: #f0f4f9;
  font-size: 18px;
}

.buttons-triggered svg {
  font-size: 17px;
}

.loader {
  width: 100%;
}

.loader hr {
  border-radius: 5px;
  border: none;
  background-color: #f6f7f8;
  background: linear-gradient(to right, #9ed7ff, #ffffff, #9ed7ff);
  background-size: 800px 50px;
  height: 20px;
  min-width: 720px;
  animation: loader 3s infinite;
}

.result-data p {
  font-size: 14px;
  line-height: 28px;
  font-weight: 300;
}

.result-title {
  padding: 20px 0;
}

.result-title p {
  font-size: 16px;
}

.result-data {
  max-height: 380px;
  overflow-y: scroll;
}

.result-data::-webkit-scrollbar {
  display: none;
}

@keyframes loader {
  0% {
    background-position: -800px 0px;
  }
  100% {
    background-position: 800px 0px;
  }
}

@media screen and (max-width: 450px) {
  .cards {
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  .card {
    max-width: 165px;
  }
  .main {
    padding: 0 20px;
  }
  .bottom-content {
    width: auto;
  }
  .footer p {
    font-size: 12px;
    text-align: center;
  }
  .footer {
    margin-top: 20px;
  }
  .result-data {
    max-height: 600px;
}
.loader hr {
  min-width: 100%; 
}
.mid-content {
  max-width: 98%;
  margin: unset;
}
.top-content {
  padding: 20px 0;
}
}
